import React, { useEffect } from "react";
import Logo from "../../Components/Navbar/Logo";
import "./style.scss";
import { useRef, useState } from "react/cjs/react.development";
import * as faceApi from "face-api.js";
import * as canvas from 'canvas'
import axios from "axios";
import { resizeResults } from "face-api.js";

const FilterPage = (props) => {
  const [width, setWidth] = useState(720);
  const [height, setHeight] = useState(560);
  const videoRef = useRef();
  const videoContaiterRef = useRef();


  useEffect(()=>{
    run();
  },[])


  useEffect(() => {
    
    const videoContaiter = document.querySelector(
      ".filtera-page__content__video"
    );
    const video = document.getElementById("filter__video");
    // const stream;
   
    function vidOff() {
      video.pause();
      video.src = "";
      video.srcObject = null;
  }

    startVideo();
    function aa() {
      setWidth(window.innerWidth);
    }
    window.onresize = aa;

    if (videoContaiter) {
      if (window.innerWidth < 520) {
        videoContaiter.style.width = `${
          window.innerWidth - window.innerWidth / 3
        }px`;
        videoContaiter.style.height = `${
          (window.innerWidth - window.innerWidth / 3) * 2
        }px`;
      } else if (window.innerWidth > 1200) {
        videoContaiter.style.width = `${
          window.innerWidth / 2 - window.innerWidth / 5
        }px`;
        videoContaiter.style.height = `${
          (window.innerWidth / 2 - window.innerWidth / 5) * 1.2
        }px`;
      }
    }
    return function(){
      vidOff()
    }
  }, [width]);

 
  function startVideo() {
    navigator.mediaDevices
      .getUserMedia({ video: {} })
      .then((stream) => {
        videoRef.current.srcObject = stream;
      })
      .catch((err) => console.log(err));
  }

  const run = async ()=>{
    try {
      Promise.all([
        faceApi.nets.tinyFaceDetector.load("../models/"),
        faceApi.nets.faceExpressionNet.load("../models/"),
        faceApi.nets.faceLandmark68Net.load("../models/"),
         faceApi.nets.ageGenderNet.load("../models/"),
         faceApi.loadFaceExpressionModel(`../models/`)
      ]).then(startVideo())
    
    } catch (e) {
      console.log(e.name, e.message, e.stack);
    }
   
    videoRef.current.addEventListener("play",  () => {
      const canvas = faceApi.createCanvasFromMedia(videoRef.current);
      videoContaiterRef.current.append(canvas)
      const displaySize = {width : videoRef.current.width, height : videoRef.current.height}
      faceApi.matchDimensions(canvas, displaySize)
      setInterval(async()=>{
        const detections = await faceApi.detectAllFaces(videoRef.current, new faceApi.TinyFaceDetectorOptions()).withFaceLandmarks().withFaceExpressions().withAgeAndGender();
        console.log(detections);
        const resizedDetections = faceApi.resizeResults(detections,displaySize);
        canvas.getContext("2d").clearRect(0,0, canvas.width, canvas.height)
        canvas.getContext("2d").clearRect(0,0, canvas.width, canvas.height)
        faceApi.draw.drawDetections(canvas, resizedDetections);
        faceApi.draw.drawFaceLandmarks(canvas, resizedDetections)
        faceApi.draw.drawFaceExpressions(canvas, resizedDetections)
      },100)
     
    })
  }

  return (
    <section className="filtera-page" ref={videoContaiterRef}>
      {/* <div className="filtera-page__container">
        <div className="filtera-page__logo">
          <Logo />
        </div>

        <div className="filtera-page__content">
          <div className="filtera-page__content__description">
            <div className="filtera-page__content__description__text">
              ldsal;dkl;akf;kla;skdasdjashdjkhasjkdhsadkjasdsdjkahduiwqydsjka
            </div>
            <div className="filtera-page__content__description__sep"></div>
            <div className="filtera-page__content__description__name">
              #TEAMMATRIX
            </div>
          </div>
          <div ref={videoContaiterRef} className="filtera-page__content__video">
            <video
            ref={videoRef}
              id="filter__video"
              style={{ backgroundColor: "green" }}
              width={width}
              height={height}
              autoPlay={true}
              muted
            ></video>
          </div>
        </div>
      </div> */}
        <video
            ref={videoRef}
              id="filter__video"
              style={{ backgroundColor: "green" }}
              width={width}
              height={height}
              autoPlay={true}
              muted
            ></video>
    </section>
  );
};

export default FilterPage;
